.logo {
  &_wrapper {
    background-color: #000;
    display: flex;
    align-items: center;
  }
  &_img {
    width: 48px;
    height: 48px;
  }
  &_title {
    color: #fff;
    font-size: 20px;
    line-height: 0.7;
    text-transform: uppercase;
    font-family: Montserrat;
    letter-spacing: normal;
    font-weight: normal;
    margin: 17px 0 17px 12px;
  }
}

@media (max-width: 1200px) {
  .logo {
    &_title {
      font-size: 15px;
    }
  }
}
